    .w-100.titulo-edit.pt-md-4
      h3 Riesgos
    
    p.mb-5(data-aos="fade-right") Toda actividad humana implica un riesgo y, sin duda, el muestreo tiene asociados algunos riesgos para la salud y seguridad de las personas que lo realizan. Para poder entender e identificar estos riesgos es importante aclarar los siguientes trminos (ISO, 2008):

    .d-flex.flex-wrap.mb-5
      img.mb-4.mb-lg-auto.col-lg-5(src="@/assets/curso/temas/tema2/img-11.png" style="max-width: 477px" data-aos="fade-right" alt="Imagen decorativa")
      .col-lg-7.ps-lg-4
        .container-card-folder
          span.container-number(data-aos="fade-right") 01
          .container-folder
            .sup-folder
            h3(data-aos="fade-right") Peligro
            .container-text-folder
              p.mb-0 Es una fuente con un potencial para causar lesiones y deterioro de la salud.
        .container-card-folder
          span.container-number(data-aos="fade-right") 02
          .container-folder
            .sup-folder
            h3(data-aos="fade-right") Riesgo
            .container-text-folder
              p.mb-0 Con frecuencia el riesgo se expresa en trminos de una combinacin de las consecuencias de un evento.
        .container-card-folder
          span.container-number(data-aos="fade-right") 03
          .container-folder
            .sup-folder
            h3(data-aos="fade-right") Riesgo para la seguridad y salud en el trabajo
            .container-text-folder
              p La probabilidad que ocurran eventos o exposiciones peligrosas relacionadas con el trabajo y la severidad de la lesin y deterioro de la salud.
                br
                br
                |El planteamiento bsico en la evaluacin de riesgos se describe con la frmula: 
              .d-flex
                p.px-3.py-1.mb-0.mr-auto.fw-bold(style="background-color: #FFA031; border-radius: 8px;") Peligro x exposicin = riesgo 
    p.mb-5(data-aos="fade-right") Es decir que, para conocer y controlar el riesgo, es fundamental identificar y estudiar los peligros asociados a una actividad.

    h4 Clasificacin de los peligros

    p.mb-5(data-aos="fade-right") De acuerdo con la Gua Tcnica Colombiana [GTC] 45, los riesgos se pueden clasificar en biolgicos, fsicos, qumicos, psicosociales, biomecnicos, condiciones de seguridad y fenmenos naturales (Icontec, 2012, p. 19).

    .d-flex.flex-wrap.mb-5
      img.col-lg-4.mb-4.mb-lg-auto(src='@/assets/curso/temas/tema2/img-14.png' data-aos="fade-right" alt="Imagen decorativa")
      ol.col-lg-8.lista-ol--cuadro.lista-ol--separador.ps-lg-4
        li 
          .lista-ol--cuadro__vineta(style="background-color: transparent" data-aos="fade-right")
            img(src="@/assets/curso/temas/tema1/decorador-1.svg" style="max-width: 20px")
          | <b>Biolgicos:</b> exposicin a virus y bacterias.
        li 
          .lista-ol--cuadro__vineta(style="background-color: transparent" data-aos="fade-right")
            img(src="@/assets/curso/temas/tema1/decorador-1.svg" style="max-width: 20px")
          | <b>Qumicos:</b> exposicin a lquidos, gases y vapores.
        li 
          .lista-ol--cuadro__vineta(style="background-color: transparent" data-aos="fade-right")
            img(src="@/assets/curso/temas/tema1/decorador-1.svg" style="max-width: 20px")
          | <b>Condiciones de seguridad:</b> locativo (superficies de trabajo irregularidades, deslizantes o con diferencia del nivel), condiciones de orden y aseo y por ltimo temas pblicos (robos, atracos, asaltos).

    .cajon.color-acento-contenido.p-4.mb-4(style="background-color: #D5F6FF" data-aos="fade-right")
      p.mb-0 A continuacin se relacionan los peligros biolgicos, peligros qumicos y as como condiciones de seguridad importantes para prevenir accidentes y lesiones. Los peligros biolgicos son agentes infecciosos y los qumicos son sustancias txicas. Es necesario tomar medidas preventivas, con el fin evitar lesiones por manejo de sustancias qumicas.

    .tarjeta.slydera-edit.p-4.mb-5(style="background-color: #F6F6F6;" data-aos="fade-left")
      SlyderA(tipo="b")
        .row.col-lg-10.mx-auto.align-items-center
          .col-md-8.mb-4.mb-md-0.order-2.order-md-1
            h4 Peligros biolgicos
            p.mb-0 Los peligros biolgicos estn bsicamente asociados a la posible exposicin con bacterias patgenas presentes en los cuerpos de agua muestreados o en las muestras tomadas. 
              br
              br
              |La exposicin a este peligro puede aumentar durante el muestreo de aguas residuales. Por ello, durante estos muestreos es indispensable contar con mscaras faciales y guantes; as mismo, debe hacer desinfeccin de los recipientes y elementos que hayan entrado en contacto con estas aguas.
          img.mb-4.mb-md-0.col-md-4.order-1.order-md-2(src='@/assets/curso/temas/tema2/sla-1.svg' alt="Imagen decorativa")
        
        .row.col-lg-10.mx-auto.align-items-center
          img.col-md-4.mb-4.mb-md-0(src='@/assets/curso/temas/tema2/sla-2.svg' alt="Imagen decorativa")
          .col-md-8.mb-4.mb-md-0
            h4 Peligros qumicos
            p.mb-0 Los peligros qumicos bsicamente se originan por la utilizacin de sustancias qumicas durante el alistamiento, toma de muestras, realizacin de ensayos <em>in situ</em>, transporte de muestras y reactivos para su anlisis y preservacin. El grado de peligrosidad de estas sustancias qumicas depender de sus propiedades intrnsecas, es decir, de su capacidad para interferir en procesos biolgicos normales, y de su capacidad para arder, explotar, corroer, entre otras.
        
        .row.col-lg-10.mx-auto.align-items-center
          .col-md-8.mb-4.mb-md-0.order-2.order-md-1
            h4 Condiciones de seguridad
            p.mb-0 En las condiciones de seguridad el control principal para evitar lesiones a causa del uso de sustancias qumicas es el conocimiento acerca de las propiedades de dichos reactivos. Para ello, las fichas de seguridad de los reactivos, as como el etiquetado, son fundamentales para el control de los riesgos qumicos. Actualmente, se utiliza un sistema a nivel internacional para ayudar en ese control de los peligros qumicos, denominado el sistema globalmente armonizado.
          img.col-md-4.mb-4.mb-md-0.order-1.order-md-2(src='@/assets/curso/temas/tema2/sla-3.svg' alt="Imagen decorativa")

    .w-100.titulo-edit.pt-md-4.mb-5
      h3 Pictogramas
    
    .d-flex.flex-wrap.mb-5
      img.col-lg-4.mb-4.lg-md-0(src='@/assets/curso/temas/tema2/img-15.svg' data-aos="fade-right" alt="Imagen decorativa")
      .col-lg-8.ps-lg-5(data-aos="fade-left")
